.toggle {
  position: relative;
  width: 36px;
  height: 36px;
  border: none;
  border-radius: 4px;
  background: transparent;
  cursor: pointer;
  outline: none;

  @media #{$tablet-width} {
    display: none;
  }

  &::before,
  &::after {
    position: absolute;
    left: 8px;
    display: block;
    content: "";
    width: 20px;
    height: 2px;
    border: none;
    background: $neuro-text;
    transition: all 0.3s ease-out;
  }

  &::before {
    top: 12px;
  }

  &::after {
    top: 22px;
  }

  &--open {
    // box-shadow: inset 1px 1px 2px rgba(174, 174, 192, 0.2), inset -1px -1px 1px rgba(255, 255, 255, 0.7);
    z-index: 10000;

    &::before {
      transform: translateY(5px) rotate(45deg);
    }

    &::after {
      transform: translateY(-5px) rotate(-45deg);
    }
  }
}
