.btn {
  display: inline-block;
  width: auto;
  padding: 10px;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-decoration: none;
  border: none;
  border-radius: 4px;
  user-select: none;
  cursor: pointer;

  &:hover,
  &:focus {
    outline: none;
  }

  &--close {
    margin-top: 20px;
  }

  &--submit {
    border: none;
    outline: none;

    @media #{$mobile-only} {
      width: 100%;
    }
  }
}
