.gallery {
  max-width: 700px;
  margin: 0 auto;

  @media #{$tablet-width} {
    width: 85% !important;
  }
}

.gallery__item {
  position: relative;
  cursor: grab;

  .iframe-parent {
    position: relative;
    padding: 0 0 56.25% 0;
    height: 0;
    overflow: hidden;

    iframe,
    object,
    embed {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
    }
  }
}

.gallery__item:focus {
  outline: none;
}

.gallery__item-desc {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 5px;
  font-size: 14px;
  text-align: left;
  background: rgba(0, 0, 0, 0.7);
}

.gallery__item-img {
  width: 100%;
}

.slick-dots {
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin-top: 5px;
  // margin-bottom: 30px;
}

.slick-dots button {
  width: 10px;
  height: 10px;
  padding: 0;
  margin: 0;
  line-height: 0;
  background: #fff;
  font-size: 0;
  outline: none;
  border: none;
  border-radius: 50%;
  transition: all 0.3s linear;
}

.slick-dots button:hover {
  cursor: pointer;
}

.slick-dots li:not(:last-child) {
  margin-right: 15px;
}

.slick-active button {
  background: $neuro-text;
}

.slick-prev,
.slick-next {
  position: absolute;
  top: 50%;
  display: flex;
  width: 40px;
  height: 40px;
  margin-top: -15px;
  padding: 0;
  font-size: 0;
  line-height: 0;
  color: transparent;
  background: transparent;
  cursor: pointer;
  border: none;
  outline: none;
  border-radius: 4px;
  z-index: 100;
  transform: translate(0, -50%);
  transition: all 0.3s linear;
}

.slick-prev::before,
.slick-next::before {
  position: absolute;
  left: 50%;
  bottom: 50%;
  display: block;
  content: "";
  width: 8px;
  height: 8px;
  margin-bottom: -5px;
  background: transparent;
  border-bottom: 3px solid $neuro-text;
  border-left: 3px solid $neuro-text;
  border-radius: 0;
}

.slick-prev::before {
  margin-left: -2px;
  transform: rotate(45deg);
}

.slick-next::before {
  margin-left: -6px;
  transform: rotate(225deg);
}

.slick-prev {
  left: -60px;
}

.slick-next {
  right: -60px;
}

.slick-next::before {
  content: "❯";
}

@media (min-width: 501px) and (max-width: 767px) {
  .slick-slide {
    margin: 0 5px;
  }

  .slick-list {
    margin: 0 -5px;
  }

  .gallery--catalog .slick-slide {
    margin: 0;
  }

  .gallery--catalog .slick-list {
    margin: 0;
  }
}

@media (min-width: 768px) {
  .gallery__item {
    width: 100%;
  }
}
