.section {
  @media #{$tablet-width} {
    margin-bottom: 50px;
  }

  @media #{$desktop-width} {
    margin-bottom: 70px;
  }

  &__description {
    @media #{$tablet-width} {
      flex-basis: 50%;
    }

    &--process {
      flex-basis: 100%;
    }
  }

  &__title {
    font-size: 21px;
    font-weight: 700;
    line-height: 1.2;
    letter-spacing: 1px;
    margin: 0;
    text-transform: uppercase;

    @media #{$small-mobile-width} {
      font-size: 24px;
    }

    @media #{$mobile-width} {
      font-size: 28px;
    }

    @media #{$desktop-width} {
      font-size: 36px;
    }
  }

  &__subtitle {
    display: block;
    margin-bottom: 30px;
    font-weight: 600;

    @media #{$desktop-width} {
      font-size: 24px;
    }
  }

  &__paragraph {

    &--process {
      max-width: 700px;
    }
  }

  &__img {
    width: auto;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;

    @media #{$tablet-width} {
      flex-basis: 50%;
      height: auto;
    }

    &--header {
      height: 220px;
      background-image: url(../img/iphone--film.png);

      @media #{$small-mobile-width} {
        height: 260px;
      }

      @media #{$mobile-width} {
        height: 300px;
      }

      @media #{$tablet-width} {
        height: 250px;
      }

      @media #{$desktop-width} {
        height: 300px;
      }
    }

    &--features {
      height: 300px;
      background-image: url(../img/iphone2.png);

      @media #{$tablet-width} {
        flex-basis: 30%;
        order: -1;
        height: 400px;
      }

      @media #{$desktop-width} {
        height: 450px;
      }
    }

    &--comparison {
      height: 250px;
      background-image: url(../img/ipad--film.png);

      @media #{$small-mobile-width} {
        height: 300px;
      }

      @media #{$mobile-width} {
        height: 350px;
      }

      @media #{$tablet-width} {
        height: 300px;
      }

      @media #{$desktop-width} {
        height: 350px;
      }
    }
  }

  &__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0;
    margin-top: 40px;
    padding: 0;
    margin-bottom: 20px;

    &-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      flex-basis: 50%;
      margin-bottom: 10px;
      list-style: none;
      text-align: center;

      @media #{$mobile-width} {
        flex-basis: 33.3333%;
      }

      div {
        width: 50px;
        height: 50px;
        background-color: $neuro;
        border-radius: 4px;
        background-position: center;
        background-size: 30px;
        background-repeat: no-repeat;
      }

      p {
        padding: 10px;
        font-size: 14px;

        // @media #{$mobile-width} {
        //   padding: 0;
        // }
      }

      &--protect div {
        background-image: url(../icons/icon-hammer.svg);
      }

      &--color div {
        background-image: url(../icons/icon-color.svg);
      }

      &--sensor div {
        background-image: url(../icons/icon-fingerprint.svg);
      }

      &--safety div {
        background-image: url(../icons/icon-safety.svg);
      }

      &--infinity div {
        background-image: url(../icons/icon-infinity.svg);
      }

      &--precision div {
        background-image: url(../icons/icon-precision.svg);
      }
    }
  }

  .process__list {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    margin: 30px 0;
    padding: 0;
    list-style: none;

    &-item {
      display: flex;
      flex-direction: column;
      align-items: center;
      flex-basis: 33.3333%;
      text-align: center;

      div {
        width: 50px;
        height: 50px;
        background-color: $neuro;
        border-radius: 4px;
        background-position: center;
        background-size: 30px;
        background-repeat: no-repeat;
      }

      &--choice div {
        background-image: url(../icons/icon-choice.svg);
      }

      &--wait div {
        background-image: url(../icons/icon-wait.svg);
      }

      &--complete div {
        background-image: url(../icons/icon-check.svg);
      }
    }
  }
}
