.header {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;

  @media #{$tablet-width} {
    height: 100vh;
    height: var(--vh, 1vh);
    margin-bottom: 50px;
  }

  @media #{$desktop-width} {
    margin-bottom: 70px;
  }

  &__top {
    @media #{$tablet-width} {
      margin-bottom: 30px;
    }

    @media #{$desktop-width} {
      margin-bottom: 50px;
    }
  }

  &__logo {
    z-index: 10000;

    @media #{$desktop-width} {
      margin-right: 30px;
    }

    img {
      @media #{$tablet-width} {
        width: 120px;
      }
    }
  }

  &__list {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    margin: 30px 0;
    padding: 0;
    list-style: none;

    &-item {
      flex-basis: 25%;

      div {
        width: 50px;
        height: 50px;
        background-color: $neuro;
        border-radius: 4px;
        background-position: center;
        background-size: 35px;
        background-repeat: no-repeat;

        @media #{$desktop-width} {
          width: 100px;
          height: 100px;
          background-size: 60px;
        }
      }

      &--watch div {
        background-image: url(../icons/icon-watch.svg);
      }

      &--phone div {
        background-image: url(../icons/icon-phone.svg);
      }

      &--pad div {
        background-image: url(../icons/icon-pad.svg);
      }

      &--camera div {
        background-image: url(../icons/icon-camera.svg);
      }
    }
  }

  &__middle {
    display: flex;

    @media #{$tablet-width} {
      margin-top: auto !important;
      margin-bottom: auto !important;
    }
  }

  // &__middle.section {
  //   margin-bottom: 0;
  // }

  &__bottom {
    display: none;
    text-align: center;

    @media #{$tablet-width} {
      display: block;
      margin: 0 auto;
      margin-bottom: 20px;
    }
  }

  &__order {
    @media #{$tablet-width} {
      margin-top: 20px;
    }
  }

  .arrow {
    display: block;
    width: 30px;
    height: 30px;
    background-image: url(../icons/icon-arrow.svg);
    background-position: center;
    background-size: 15px;
    background-repeat: no-repeat;
  }
}
