.contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
  margin-bottom: 20px;

  @media #{$tablet-width} {
    margin: 0;
  }

  & > *:not(:last-child) {
    margin-right: 15px;
  }

  &__tel {
    flex-basis: 100%;
    margin-bottom: 20px;
    margin-right: 0 !important;
    padding: 0 15px;
    border-radius: 4px;

    @media #{$mobile-width} {
      flex-basis: auto;
      margin-bottom: 0;
    }

    @media #{$tablet-width} {
      padding-top: 0;
      padding-bottom: 0;
    }

    a {
      width: 100%;
      padding: 10px;
      font-size: 16px;
      font-weight: 700;
      text-decoration: none;
      transition: all 0.3s;
    }

    a[href^="https"] {
      font-size: 0;
    }
  }
}
