.form {
  position: relative;
  max-width: 500px;
  margin-bottom: 30px;
}

.form__caption {
  display: block;
  margin-bottom: 20px;
  font-size: 14px;
  color: $neuro-text;
}

.form__caption sup {
  font-size: 14px;
}

.form__input {
  display: block;
  width: 100%;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 14px;
  font-weight: 600;
  border: none;
  border-radius: 4px;
}

.form__input:focus {
  outline: none;
}

.form__input::-webkit-input-placeholder {
  color: rgba($color: $neuro-text, $alpha: 0.7);
}

.form__input:-moz-placeholder,
.form__input::-moz-placeholder {
  color: rgba($color: $neuro-text, $alpha: 0.7);
}

.form__input:-ms-input-placeholder {
  color: rgba($color: $neuro-text, $alpha: 0.7);
}

.textarea-question {
  resize: none;
}

.pageclip-form__error,
.pageclip-form__success {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  font-weight: bold;
  text-align: center;
  background: rgba($color: $neuro, $alpha: 1.0);
  z-index: 100;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 3px 3px 7px rgba(174, 174, 192, 0.4), -3px -3px 7px $white;

  &:hover,
  &:focus {
    box-shadow: 3px 3px 7px rgba(174, 174, 192, 0.4), -3px -3px 7px $white;
  }
}

.pageclip-form__error.hidden {
  display: none;
}

.pageclip-form__error .form-close {
  padding: 0;
  margin: 0;
}

.pageclip-form__success {
  align-items: center;
  font-size: 16px;
  font-weight: 700;
  color: $black;
}

.pageclip-form__submit::after {
  margin-top: -12px;
  border-color: $neuro-accent;
  border-left-color: darken($color: $neuro-accent, $amount: 30);
}

.form-close {
  position: absolute;
  width: 25px;
  height: 25px;
  top: 5px;
  right: 5px;
}

.form-close:after,
.form-close:before {
  display: block;
  content: "";
  position: absolute;
  top: 12px;
  right: 5px;
  width: 15px;
  height: 2px;
  background: $neuro-text;
  z-index: 100;
  cursor: pointer;
}

.form-close:before {
  transform: rotate(45deg);
}

.form-close:after {
  transform: rotate(-45deg);
}

.pageclip-form__error-text {
  padding: 10px;
}
