.nav {
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: center;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  background: $neuro;
  overflow: auto;
  z-index: 1000;
  animation: fadeInUp 0.3s linear;

  @media #{$tablet-width} {
    display: block !important;
    position: relative;
    animation: none;
  }

  &--closed {
    display: none;
  }
}

.nav__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000000;

  @media #{$tablet-width} {
    display: block !important;
    position: relative;
  }
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;

  @media #{$tablet-width} {
    display: flex;
    align-items: center;
  }
}

.nav__list .contacts {
  @media #{$tablet-width} {
    margin: 0;
    margin-left: auto;
  }

  &__tel {
    flex-basis: 100%;
    margin-bottom: 15px;

    @media(min-width: 1024px) {
      flex-basis: auto;
      margin-bottom: 0;
    }
  }
}

.nav__item a {
  display: block;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 26px;
  line-height: 1;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;

  @media #{$tablet-width} {
    font-size: 16px;
    margin-right: 10px;
    margin-bottom: 0;
  }

  @media (min-width: 1080px) {
    margin-left: 10px;
  }

  &.order {
    margin-bottom: 0;

    @media #{$tablet-width} {
      font-size: 16px;
      margin-right: 0;
    }

    @media #{$desktop-width} {
      margin-left: 0;
    }

    @media (min-width: 1080px) {
      margin-left: 10px;
    }
  }
}

.nav__address-time {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0;
  margin: 0;
}

.nav__address,
.nav__time {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  font-size: 14px;
}

.nav__address {
  margin-bottom: 10px;
}

.nav__address img,
.nav__time img {
  margin-right: 10px;
}

.nav__address div,
.nav__time div {
  text-align: left;
}

.nav--closed {
  display: none;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media only screen and (max-device-width: 700px) and (orientation: landscape) {
  .page-header__title {
    font-size: 18px;
  }

  .page-header__separator {
    display: none;
  }

  .page-header__subtitle {
    font-size: 16px;
  }

  .page-header__paragraph {
    font-size: 14px;
  }

  .nav__list .contacts {
    margin-top: 10px;

    @media #{$tablet-width} {
      margin: 0;
    }
  }

  .nav__item a {
    display: block;
    padding: 5px 0;
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;
    text-decoration: none;
  }
}

@media (min-width: 768px) {
  .nav {
    position: relative;
    flex-grow: 1;
    overflow: inherit;
    background: transparent;
  }
}
