$screen-xs: 360px;
$screen-sm: 576px;
$screen-md: 768px;
$screen-lg: 992px;
$screen-xl: 1200px;
$screen-xxl: 1600px;

$mobile-only: "(max-width: #{$screen-sm})";
$small-mobile-width: "(min-width: #{$screen-xs})";
$mobile-width: "(min-width: #{$screen-sm})";
$tablet-width: "(min-width: #{$screen-md})";
$laptop-width: "(min-width: #{$screen-lg})";
$desktop-width: "(min-width: #{$screen-xl})";
$large-desktop-width: "(min-width: #{$screen-xxl})";

@mixin container-fluid {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  padding: 16px;

  @media #{$tablet-width} {
    width: 95%;
    margin: 0 auto;
    padding-left: 0;
    padding-right: 0;
  }
  @media #{$desktop-width} {
    max-width: 1200px;
  }
}

@mixin container-fluid--vertical {
  width: 100%;
  padding: 16px;
  position: relative;

  @media #{$tablet-width} {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 95%;
    margin: 0 auto;
    padding: 0;
  }

  @media #{$desktop-width} {
    max-width: 960px;
  }

  @media #{$large-desktop-width} {
    max-width: 1200px;
  }
}
